.chats {
    display: flex;
align-items: center;
justify-content: space-between;
padding: 25px;
height: 120px;
border-bottom: 2px solid #fafafa;
}

.chats__detalles {
    flex: 1;
}

.chats__detalles > p {
    color: gray;
}

.chats__time {
    color: lightgray;
}

a {
    text-decoration: none;
    color: inherit;
}

.chats__avatar {
    margin-right: 20px;
}