.tarjetas__contenedor {
  display: flex;
  justify-content: center;
  cursor: pointer;
}
.tarjeta {
  position: relative;
  width: 600px;
  height: 60vh;
  max-width: 85vh;
  padding: 20px;
  box-shadow: 0 10px 15px 0 rgba(0, 0, 0, 0.3567);
  border-radius: 20px;
  background-size: cover;
  background-position: center;
}

.persona__info {
  position: absolute;
  bottom: 50px;
  color: white;
  font-size: 40px;
  display: flex;
  align-items: center;

}

.persona__info h2 {
    font-size: 2rem;
}

.persona__info p {
    font-size: 1.4rem;
    margin-left: 10px;
    margin-top: 5px;
}

.distancia {
    position: absolute;
    color: white;
    font-size: 1.4rem;
    bottom: 25px;
    font-weight: 300;
}

.swipe {
    position: absolute;
    margin-top: 10px;
}

@media screen and (max-width: 480px){
  .tarjetas__contenedor{
    width: 90%;
    margin: 0 auto;
  }

  .tarjeta{
    max-width: 80vw;
  }

  .persona__info {
    bottom: 80px;
  }
}
