.botones-swipe {
  display: flex;
  width: 100%;
  position: fixed;
  bottom: 7vh;
  justify-content: space-evenly;
}

.botones-swipe .MuiIconButton-root {
background-color: white;
box-shadow: 0 10px 50px 0 rgba(0, 0, 0, 0.589);
}

#btn-replay {
  color: orange;
  }

.swipe__btn {
    background-color: white;
}

.botonesSwipe__close {
  color: red;
}

.botonesSwipe__star {
    color: rgb(64,168,233);
}

.botonesSwipe__fav {
    color: rgb(82, 165, 82);
}

.botonesSwipe__flash {
    color: purple;
}
